<template>
  <section>
    <div class="stats--center">

      <div class="stats__typing">
        <div
          class="stats__typing__type"
          :class="'pokemon-'+type_"
          v-for="(type_, key) in data.types"
          :key="key"
        >
          {{type_}}
        </div>
      </div>

      <section class="stats--center stats--smaller">
        <div class="stats__abilities">
          <div
            class="stats__abilities__ability"
            v-for="(ability, key) in data.abilities"
            :key="key"
          >
              <p class="small stats__abilities__ability__name">#{{key+1}} {{ability[0].name}} <span class="small">[{{Number(Math.round(ability[1]+'e2')+'e-2')}}<span class="smaller">%</span>]</span></p>
              <p class="stats__abilities__ability__effect">{{ability[0].description}}</p>
          </div>
        </div>

        <div class="stats__stats">
          <div
            class="stats__gauge"
            v-for="(stat, key) in data.stats"
            :key="key"
          >
            <p class="stats__gauge--absolute">{{ label[key] }}: {{stat}}</p>
            <div class="stats__gauge__bar" :class="'stats--'+label[key]" :style="gaugeBar(stat)"></div>
          </div>
        </div>
      </section>
    </div>
  </section>
</template>

<script>
export default {
  props: ["data"],
  data() {
    return {
      label: ["hp", "atk", "def", "spa", "spd", "spe"]
    };
  },
  methods:{
    gaugeBar(stat){
      stat =  ( (stat + 20 ) / 140 )
      return { width: stat+'%' }
    }
  }
};
</script>